ul.claim-photo-section {
  overflow: auto;
  .blueimp-gallery > .indicator > li {
    width: 30px;
    height: 30px;
    border-radius: 15px;
  }

  li.claim-photo-item {
    .hovereffect {
      width:100%;
      height:100%;
      float:left;
      overflow:hidden;
      position:relative;
      text-align:center;
      cursor:default;
    }

    .hovereffect .overlay-pane {
      width:100%;
      height:100%;
      position:absolute;
      overflow:hidden;
      top:0;
      left:0;
      opacity:0;
      background-color:rgba(0,0,0,0.5);
      -webkit-transition:all .2s ease-in-out;
      transition:all .2s ease-in-out
    }

    .hovereffect img {
      display:block;
      position:relative;
      -webkit-transition:all .4s linear;
      transition:all .4s linear;
    }

    .hovereffect h2 {
      text-transform:uppercase;
      color:#fff;
      text-align:center;
      position:relative;
      font-size:17px;
      background:rgba(0,0,0,0.6);
      -webkit-transform:translatey(-100px);
      -ms-transform:translatey(-100px);
      transform:translatey(-100px);
      -webkit-transition:all .1s ease-in-out;
      transition:all .1s ease-in-out;
      padding:10px;
    }

    .hovereffect a.info {
      text-decoration:none;
      display:inline-block;
      text-transform:uppercase;
      color:#fff;
      border:1px solid #fff;
      background-color:transparent;
      opacity:0;
      filter:alpha(opacity=0);
      -webkit-transition:all .1s ease-in-out;
      transition:all .1s ease-in-out;
      margin:50px 5px 0px 5px;
      padding:7px;
      height: auto;
      i.fa {
        width: 20px;
        height: 20px;
        font-size: 16px;
        padding: 0px;
      }      
    }

    .hovereffect a.info:hover {
      box-shadow:0 0 5px #fff;
    }

    .hovereffect:hover img {
      -ms-transform:scale(1.2);
      -webkit-transform:scale(1.2);
      transform:scale(1.2);
    }

    .hovereffect:hover .overlay-pane {
      opacity:1;
      filter:alpha(opacity=100);
    }

    .hovereffect:hover h2,.hovereffect:hover a.info {
      opacity:1;
      filter:alpha(opacity=100);
      -ms-transform:translatey(0);
      -webkit-transform:translatey(0);
      transform:translatey(0);
    }

    .hovereffect:hover a.info {
      -webkit-transition-delay:.1s;
      transition-delay:.1s;
    }
  }
}

.doc-file-modal {
  .modal-header {
    padding: 0px;
    border-bottom-color: #ddd;
    h4.modal-title {
      padding: 13px 15px;
    }
    button {
      float: right;
      padding: 15px;
      background: #FFF;
      border: 0px;
      border-left: solid 1px #ddd;
      text-align: center;
      width: 50px;
      height: 51px;
      margin-top: 0px;
      font-size: 17px;
      color: #d6d6d6;
      opacity: 1;
    }
    button:hover {
      color: #bbb;
      background: #EEE;
    }
  }
  .modal-footer {
    border-top-color: #ddd;
  }
  .file-info-form {
    padding: 10px;
    height: 330px;
    background: #f3f3f3;
    border-left: solid 1px #ddd;
    label {
      color: #666;
      font-weight: normal;
    }
  }
  .col-xs-8, .col-xs-4 {
    padding: 0px;
  }
  .fileObject {
    text-align: center;
    padding: 15px 0px;
    img {
      max-width: 100%;
      max-height: 300px;
    }
  }

  .panel-loading {
    min-height: 100px;
    opacity:0.45;
    -moz-opacity:0.45;
    filter:alpha(opacity=45);
    background-image: url(../img/ajax-loader.gif);
    background-repeat: no-repeat;
    background-position: center center;
    z-index:100000;
  }
}

